.sl-elements {
    font-size: 13px;
    padding-right: 65px;
}

.sl-elements .svg-inline--fa {
    display: inline-block
}

.sl-elements .DocsSkeleton {
    animation: skeleton-glow .5s linear infinite alternate;
    background: rgba(206, 217, 224, .2);
    background-clip: padding-box !important;
    border-color: rgba(206, 217, 224, .2) !important;
    border-radius: 2px;
    box-shadow: none !important;
    color: transparent !important;
    cursor: default;
    pointer-events: none;
    user-select: none
}

.sl-elements .Model {
    --fs-code: 12px
}

.sl-elements .ElementsTableOfContentsItem:hover {
    color: inherit;
    text-decoration: none
}

.sl-elements .ParameterGrid {
    align-items: center;
    display: grid;
    grid-template-columns: fit-content(120px) 20px auto;
    margin-bottom: 16px;
    padding-bottom: 0;
    row-gap: 3px
}

.sl-elements .TryItPanel>:nth-child(2) {
    overflow: auto
}

.sl-elements .OperationParametersContent {
    max-height: 162px
}

.sl-elements .Checkbox {
    max-width: 15px;
    padding-right: 3px
}

.sl-elements .TextForCheckBox {
    padding-left: 9px;
    padding-top: 6px
}

.sl-elements .TextRequestBody {
    margin-bottom: 16px;
    max-height: 200px;
    overflow-y: auto;
    padding-bottom: 0
}

.sl-elements .HttpOperation .JsonSchemaViewer .sl-markdown-viewer p,
.sl-elements .HttpOperation__Parameters .sl-markdown-viewer p,
.sl-elements .Model .JsonSchemaViewer .sl-markdown-viewer p {
    font-size: 12px;
    line-height: 1.5em
}

.sl-flex.sl-flex-col.sl-w-full.sl-p-4 {
